body {
  --primary-color: #1e3050;
  --primary-color-light: #f2f5ff;
  --white: #ffffff;
  color: var(--primary-color);
  background-color: var(--primary-color-light);
}

button,
input,
a {
  font: inherit;
  background-color: transparent;
  color: currentColor;
  text-decoration: none;
}

input::placeholder {
  color: currentColor;
  opacity: 0.3;
}

img,
svg,
picture {
  display: block;
}

button,
option,
select {
  cursor: pointer;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

.app {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.btn {
  border-radius: 5px;
  font-weight: bold;
  height: 2rem;
  outline: none;
  border: none;
}

.btn.filled {
  background-color: var(--primary-color);
  color: var(--white);
}

.btn.outline {
  border: 1px solid currentColor;
  color: currentColor;
  background-color: transparent;
}

.btn.outline:hover {
  background-color: var(--primary-color);
  color: var(--white);
}

.btn:disabled {
  background-color: lightgray;
  color: black;
  border: lightgray;
}

.btn:disabled:hover {
  cursor: not-allowed;
  background-color: lightgray;
  color: black;
}
